import React from 'react';
import logo from './logo.svg';
import './App.css';

import { Button } from "@blueprintjs/core";
import { Column, Table } from "@blueprintjs/table";

let webappos = window.webappos;

class MountTable extends React.Component {

  collectMountPointsRecursively(json, arr, path) { // path is with the trailing "/"
    for (let key in json) {
      if (typeof json[key] === "string") {
        arr.push({
          mountPoint: path + key,
          location: json[key]
        });
      }
      else {
        this.collectMountPointsRecursively(json[key], arr, path + key + "/");
      }
    }
    return arr;
  }

  readRegistry() {
    let myThis = this;
    webappos.webcall("webappos.getUserRegistryValue", "fs_mount_points").then((resp) => {
      if (resp.result) {
        console.log("registry data",resp.result);
        myThis.setState(() => {
          mount_points: myThis.collectMountPointsRecursively(resp.result, [], "")
        });
      }
    });
  }

  constructor(props) {
    super(props);

    this.state = {
      mount_points: [],
      supported_fs: []
    };
    let myThis = this;
    webappos.request_scopes("webappos_scopes", "login").then(() => {

      webappos.webcall("webappos.getSupportedFileSystems").then((resp) => {
        if (resp.result) {
          console.log("supported_fs",resp.result);
          myThis.setState(() => ({
            supported_fs: resp.result
          }), () => {
            myThis.readRegistry();
          });
        }
      });
    });

  }

  componentDidMount() {
    console.log("state1", this.state);
    /*  let myThis = this;
      webappos.request_scopes("webappos_scopes", "login").then(()=>{
  
        webappos.webcall_and_wait("webappos.getSupportedFileSystems").then((resp)=>{
          if (resp.result) {
            myThis.setState(()=>({
              supported_fs: resp.result
            }), ()=>{
              myThis.updateState();
            });
          }
        });
      });*/
  }

  componentDidUpdate() {
    console.log("state2", this.state);
  }

  render() {
    return <div>{this.state.supported_fs.length} and {this.state.mount_points}</div>;
  }

}

// TODO: table, allow to change, when ok/apply -> store in registry fs_mount_point each mount point, then re-read the registry and re-collectMountPointsRecursively and setState
// button + adds a new line
// button - for each line

function App() {


  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <p>
        </p>
        <MountTable />
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}



export default App;
